.fancy {
    background-color: transparent;
    border: 2px solid var(--title-color-dark);
    border-radius: 15px;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    font-weight: 700;
    letter-spacing: 0.05rem;
    margin: 0;
    outline: none;
    overflow: visible;
    padding: 1.125rem 1.625rem;
    position: relative;
    text-align: center;
    text-decoration: none;
    text-transform: none;
    transition: all 0.3s ease-in-out;
    user-select: none;
    font-size: 13px;
}

.fancy::before {
    content: " ";
    width: 1.525rem;
    height: 2px;
    background: var(--title-color-dark);
    top: 50%;
    left: 1.5rem;
    position: absolute;
    transform: translateY(-50%);
    transform-origin: center;
    transition: background 0.3s linear, width 0.3s linear;
}

.fancy .button__text {
    font-size: 1.125rem;
    line-height: 1.33333rem;
    padding-left: 2rem;
    display: block;
    text-align: left;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
    color: var(--title-color-dark);
}

.fancy .top-key {
    height: 2px;
    width: 1.525rem;
    top: -2px;
    left: 0.625rem;
    position: absolute;
    background: var(--body-color);
    transition: width 0.5s ease-out, left 0.3s ease-out;
}

.fancy .bottom-key-1 {
    height: 2px;
    width: 1.5625rem;
    right: 1.875rem;
    bottom: -2px;
    position: absolute;
    background: var(--body-color);
    transition: width 0.5s ease-out, right 0.3s ease-out;
}

.fancy .bottom-key-2 {
    height: 2px;
    width: 0.625rem;
    right: 0.625rem;
    bottom: -2px;
    position: absolute;
    background: var(--body-color);
    transition: width 0.5s ease-out, right 0.3s ease-out;
}

.fancy:hover {
    color: var(--body-color);
    background: var(--title-color-dark);
}

.fancy:hover::before {
    width: 0.9375rem;
    background: var(--body-color);
}

.fancy:hover .button__text {
    color: var(--body-color);
    padding-left: 1.5rem;
}

.fancy:hover .top-key {
    left: -2px;
    width: 0px;
}

.fancy:hover .bottom-key-1,
.fancy:hover .bottom-key-2 {
    right: 0;
    width: 0;
}
